class SearchBar extends React.Component {
    constructor() {
        super();
        this.state={
            open:false
        }
    }
    onMouseEnter(event){
        this.setState({open:true});
    }
    onMouseLeave(event){
        this.setState({open:false});
    }
    render() {
        var sublistClassName = "down-menu",arrowClassName="angle-gray-down";
        if(!this.state.open) {
            sublistClassName += " hide";
            arrowClassName= "angle-gray-down";
        }
        else {
            sublistClassName += " show";
            arrowClassName= "angle-gray-up";
        }



        return (
            <div className="searchBarContainer">
                <ul className="searchBar">
                    <li onMouseOver={this.onMouseEnter.bind(this)} onMouseOut={this.onMouseLeave.bind(this)}>city
                        <span className={arrowClassName}></span>
                        <ul className={sublistClassName}>
                            <li>beijing</li>
                            <li>shanghai</li>
                            <li>shenzhen</li>
                        </ul>
                    </li>
                    <li>
                        main job1
                        <ul className="down-menu">
                            <li>sub job 1 1</li>
                            <li>sub job 1 2</li>
                            <li>sub job 1 3</li>
                        </ul>
                    </li>
                    <li>
                        main job2

                        <ul className="down-menu">
                            <li>sub job 2 1</li>
                            <li>sub job 2 2</li>
                            <li>sub job 2 3</li>
                        </ul>
                    </li>
                    <li>
                        main job3

                        <ul className="down-menu">
                            <li>sub job 3 1</li>
                            <li>sub job 3 2</li>
                            <li>sub job 3 3</li>
                        </ul>
                    </li>
                    <li>
                        main job4

                        <ul className="down-menu">
                            <li>sub job 4 1</li>
                            <li>sub job 4 2</li>
                            <li>sub job 4 3</li>
                        </ul>
                    </li>
                    <li>
                        main job5
                    </li>
                </ul>
                <div>
                    <input placeholder="enter job"/>
                    <button value="search">search</button>
                </div>
            </div>
        )
    }

}

module.exports = SearchBar;


